import { Fragment, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate,useLocation } from "react-router-dom";
import { State } from "../../redux/Types";
import { userSlice } from "../../redux/User";

interface Props {
  helloBar: boolean;
  setHelloBar: React.Dispatch<React.SetStateAction<boolean>>;
  handleNavigation?: () => void;
  tab?: string;
}

const HelloBar = ({
  helloBar,
  setHelloBar,
  handleNavigation = () => {},
  tab = "",
}) => {
  const { userName } = useSelector((state: State) => state.user);
  const trial = useSelector(
    (state: State) => state.user.plan?.["trial"] ?? null,
  );
  const dispatch = useDispatch();
  const router = useNavigate();
  const location = useLocation();
  const helloBarPath =
    !window.location.pathname.includes("agent") &&
    !window.location.pathname.includes("onboarding") &&
    !window.location.pathname.includes("dashboard");

  useEffect(() => {
    const helloBarEle = document.querySelector(".hello-bar") as HTMLElement;
    const helloP = document.querySelector(".hello-p") as HTMLElement;
    const aiBotWrapper = document.querySelector(
      ".ai-bot-wrapper",
    ) as HTMLElement;

    const aiBotContainer = document.querySelector(
      ".ai-bot-container",
    ) as HTMLElement;
    if (!helloBar && helloP) {
      helloP.style.paddingTop = "";
      return;
    }
    if (!helloBar && (aiBotWrapper || aiBotContainer)) {
      window.screen.width > 990
        ? (aiBotWrapper.style.marginTop = "")
        : (aiBotContainer.style.paddingTop = "");
      return;
    }

    if (helloBarEle) {
      helloBarEle.style.zIndex = "98";
      const pt = window.screen.width > 540 ? 30 : 20;
      const paddingTopValue = helloBarEle.offsetHeight + pt;
      if (helloP) helloP.style.paddingTop = paddingTopValue + "px";
    } else if (helloP) helloP.style.paddingTop = "";
    if (aiBotWrapper || aiBotContainer) {
      const heightValue = helloBarEle.offsetHeight;
      window.screen.width > 990
        ? (aiBotWrapper.style.marginTop = heightValue + 20 + "px")
        : (aiBotContainer.style.paddingTop = heightValue + "px");
    }
  }, [trial, helloBar, tab]);

  useEffect(() => {
    if (trial || trial == null) setHelloBar(true);
  }, [trial]);

  return (
    <Fragment>
      {helloBar && (trial || trial === null) ? (
        <div className={`hello-bar ${helloBarPath ? "builder-hello-bar" : ""}`}>
          <div className="bar-close" onClick={() => setHelloBar(false)}>
            <svg
              width="16"
              height="16"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M14.6693 14.6667L1.33594 1.33337M14.6693 1.33337L1.33594 14.6667"
                stroke="white"
                strokeWidth="2"
                strokeLinecap="round"
              />
            </svg>
          </div>

          <div className="bar-content flex-cnt">
            <p className="bar-text">
              Hey <span className="bar-username">{userName}</span>, You can get
              an extra 15% off on annual plans (so 30% less than the monthly
              rate!)
            </p>
            <button
              className="bar-cta"
              onClick={() => {
                if (location.pathname === '/agent/cynthia') {
                  handleNavigation();
                  return;
                }
                router(`/settings?checked=${true}`);
                dispatch(userSlice.actions.setSettings(2));
              }}
            >
              Upgrade now
            </button>
          </div>
        </div>
      ) : (
        <></>
      )}
    </Fragment>
  );
};

export default HelloBar;
